  import CubePage from './components/cube-page.vue'
  import { customData } from './components/swipe'
  export default {
    name: 'cartIndex',
    data (){
      return {
        goods_id: '',//商品id
        key: '', //商品key
        count:'', //商品数量
        list: [],//购物车列表
        number:0,
        checked: false, //全选
        option: {
          label: '全选',
          value: false
        },
        optionNo: {
          disabled: true
        },
        swipeData: customData
      }
    },
    components: {
      CubePage
    },
    computed: {

    },
    methods: {
      showToastTxtOnly(text) {
        this.toast = this.$createToast({
          txt: text,
          type: 'txt'
        })
        this.toast.show()
      },
      getIndexData(){
        this.http.get(this.ports.goods.collection, res =>{
          this.$store.commit('changeLoading',false)
          console.log(res)
          if(res.success){
            let data = res.data
            this.list = data.res
            this.list.forEach((event)=>{
              this.$set(event,'checked',false)
              this.$set(event,'btns',[
                {
                  action: 'delete',
                  text: '删除',
                  color: '#ff3a32'
                }
              ])
            })
            console.log(this.list)
          }else{
            this.list = []
//            this.showToastTxtOnly(res.msg)
          }
        })
      },
      onItemClick(item) {
//        alert('click item:', item)
      },
      onBtnClick(btn, index) {
        if (btn.action === 'delete') {
          this.$createActionSheet({
            title: '确认要删除吗',
            active: 0,
            data: [
              {content: '删除'}
            ],
            onSelect: () => {
              console.log(this.list[index].goods_id)
              this.http.delete(this.ports.goods.collection+'/'+this.list[index].goods_id, res =>{
                if(res.success){
//                  let data = res.data
//                  this.list = data.res
                  this.list.splice(index, 1)
                }else{
                  this.showToastTxtOnly(res.msg)
                }
              })
            }
          }).show()
        } else {
          this.$refs.list[index].shrink()
        }
      },
    },
    mounted (){
      this.getIndexData()
    }
  }



